<template>
    <div>
        <div class="det active" v-for="foodItem in foods" @touchend="jumpToGoods(foodItem)">
             <div class="det-con">
                <div class="int-img">
                    <img :src="foodItem.image" alt="">
                </div>
                <div class="int-intro">
                    <h4>{{foodItem.name}}</h4>
                    <p class="det-des">{{foodItem.description}}</p>
                    <p class="det-sale">月售{{foodItem.sellCount}}份 <span>好评率{{foodItem.rating}}%</span></p>
                    <div class="det-price">
                        <span class="price">
                            ￥{{foodItem.price}} <span v-if="foodItem.oldPrice">￥{{foodItem.oldPrice}}</span>
                        </span>
                        <add-minus :food-item="foodItem"></add-minus>
                    </div>
                </div>
            </div> 
        </div>
    </div>
   
</template>

<script>
import AddMinus from '@/components/public/AddMinus'
export default{
    props:['foods'],
    components:{
        AddMinus
    },
    methods:{
        jumpToGoods(foodItem){
            this.$router.push({
                name:'goods',
                params:{
                    food:foodItem
                }
            })
        }
    }

}
</script>

<style scoped lang="scss">
    @import "@/assets/css/public/shopElement.scss"
</style>