<template>
    <div>
        <section class="shop" >
            <div class="menu-list">
                <ul :class="{'isFixed':isFixed}">
                    <li v-for="(item,index) in goods">
                        <a href="javascript:void(0)" @touchend="selectFoods(index)" :class="{'selected':showIndex == index ? true:false}">{{item.name}}</a>   
                    </li>
                </ul>
            </div>
            <div class="menu-detail" ref="detail">
                <div class="detail" v-for="(item,index) in goods" >
                    <p class="food-name" :ref="'item'+index" >{{item.name}}</p>
                    <!-- <div class="det active" v-for="foodItem in item.foods" @touchend="jumpToGoods(foodItem)"> -->
                    <shop-element :foods="item.foods"></shop-element> 
                    <!-- </div> -->
                </div>
            </div>
        </section>
        <elem-footer></elem-footer>
    </div>
     
</template>

<script>
import {mapMutations,mapState} from 'vuex'
import AddMinus from '@/components/public/AddMinus'
import ShopElement from '@/components/public/ShopElement'
import ElemFooter from '@/components/public/ElemFooter'
export default{
    props:['goods','isFixed','navHeight','navOffset','searchHeight'],
    components:{
        AddMinus,
        ShopElement,
        ElemFooter
    },
    data(){
        return{
            showIndex:0,
        }
    },
    created(){
        console.log(this.$route.params)
    },
    methods:{
        selectFoods(index){
            this.showIndex = index;
            let el = this.$refs['item'+index];
            if(el[0].offsetTop > this.navOffset + this.navHeight){
                if(this.isFixed){
                    this.$emit('changeFix',{fix:true,dis:el[0].offsetTop - this.navHeight - this.searchHeight });
                }else{
                    this.$emit('changeFix',{fix:true,dis:el[0].offsetTop - 2 * this.navHeight - this.searchHeight});
                }
            }else{
                this.$emit('changeFix',{fix:false,dis:0});
            }
        },
        
    },
    beforeRouteLeave(to,from,next){
        // const flag = window.confirm('确定要离开吗');
        // if(flag){
            // console.log(to,from)
            window.scrollTo(0,0);
            next();
        // }
    },
    // beforeRouteEnter(to,from,next){
    //     window.scrollTo(0,0);
    //     next();
    // }
}
</script>

<style lang="scss" scoped>
    @import "@/assets/css/merchant/shop.scss"
</style>
